$medium: 768px;
$large: 1060px;
$xlarge: 1180px;

@mixin mymed($media) {
  @if $media == mscr {
    @media only screen and (min-width: $medium) {
    	@content; } }

  @else if $media == lscr {
    @media only screen and (min-width: $large) {
    	@content; } }

  @else if $media == xscr {
    @media only screen and (min-width: $xlarge) {
    	@content; } } }


@mixin MainFontB() {
	font-family: MainFont, Helvetica, sans-serif;
	font-weight: bold; }
@mixin SecondFontL() {
	font-family: SecondFont, Helvetica, sans-serif;
	font-weight: 300; }
@mixin MainFont() {
	font-family: MainFont, Helvetica, sans-serif; }
@mixin ptsans() {
	font-family: pt_sans, Helvetica, sans-serif; }
@mixin font_404() {
  font-family: anodyne, Helvetica, sans-serif; }
